.inavii-social-feed-app {
  &__layouts {
    &-cards {
      padding: 30px 15px 40px;
      height: 100%;
      border: 2px solid #EEEEEE;
      flex: 1;

      &:hover,
      &.active {
        border-color: #7a3cff;
        box-shadow: 0 1px 2px -2px rgb(122 60 255 / 16%), 0 3px 6px 0 rgb(122 60 255 / 12%), 0 5px 12px 4px rgb(122 60 255 / 9%)
      }

      &-back {
        opacity: 0;
        visibility: hidden;
        transition: all .2s;
        pointer-events: none;

        .inavii-button-pro-layout {
          background-color: #7a3cff!important;

          &:hover {
            background-color: #9c66ff!important;
          }
        }
      }

      &-icon-star-box {
        position: absolute;
        top: 6px;
        right: 6px;
        height: 20px;
        width: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        background-color: #7A3CFF;
      }

      &-icon-star-box {
        color: #ffffff;
        font-size: 12px;
        z-index: 1;
      }

      &.disable {
        overflow: hidden;

        .inavii-social-feed-app__layouts-cards-back {
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          gap: 8px;
          backdrop-filter: blur(3px);
        }

        &:hover {
          .inavii-social-feed-app__layouts-cards-back {
            opacity: 1;
            visibility: visible;
            pointer-events: all;
          }
        }
      }

      .ant-card-cover {
        height: 100%;

        .ant-image {
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
        }

        img {
          max-width: max-content;
          max-height: 100%;
          border-radius: 0;
        }
      }

      .ant-card-body {
        padding: 8px 0 0;
      }
    }
  }

  &__layouts-box-scroll {
    text-align: right;

    * {
      text-align: center;
    }
  }

  &__layouts-scroll {
    position: sticky;
    top: 50%;
    transform: rotate(-90deg) translate(-22px, 70px);
    border-radius: 20px;
    height: 26px;
    padding: 4px 12px;
    line-height: 1;
    display: inline-flex;
    align-items: center;
    letter-spacing: 1px;

    @keyframes move {
      0% {
        transform: translateX(0);
      }
      50% {
        transform: translateX(-5px);
      }
      100% {
        transform: translateX(0);
      }
    }

    .ant-btn-icon {
      margin-inline-end: 4px!important;
      animation: move 1s infinite;
    }
  }
}