.inavii-social-feed-app__guides {
  .vertical-timeline-element-title {
    font-size: 16px;
  }
}

.inavii-social-feed-app {
  &__discovery-box {
    flex-direction: column;
    background-color: #5A94FF1A;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
    border: none;
    padding: 40px;
    margin-bottom: 60px;
  }

  &__discovery-description {
    font-size: 16px;
    color: #000000;
    margin-top: 20px;
    text-align: left;
    line-height: 1.6;
  }

  .vertical-timeline {
    width: 100%;
    max-width: 100%;
    padding: 0;
  }

  .vertical-timeline-element-content-arrow,
  .vertical-timeline-element-date {
    display: none;
  }

  .vertical-timeline-element-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #ffffff;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2);

    .anticon {
      height: 20px;
      width: 20px;
      svg {
        height: 20px;
        width: 20px;
        margin: 0;
        top: 0;
        left: 0;
      }
    }
  }

  .vertical-timeline::before {
    background-color: #fff;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2);
  }

  .vertical-timeline-element-content {
    background-color: #ffffff;
    text-align: left;
    border-radius: 8px;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
  }

  &__discovery-title,
  .vertical-timeline-element-title {
    font-weight: 600;
    color: #000000;
    font-family: "Montserrat", sans-serif;
  }

  .vertical-timeline-element-title {
    font-size: 18px;
  }

  &__discovery-description,
  .vertical-timeline-element-description {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.6;
    font-family: "Montserrat", sans-serif;
    color: #000000;
  }

  &.inavii-social-feed-app__discovery-container {
    background-color: #f8fafc;
  }
}