.inavii-social-feed-app {
  &__tag-button-delete {
    font-size: 12px;
  }

  &__table-pagination {
    .ant-pagination {
      justify-content: center;
    }
  }

  &__hashtag-name {
    margin-right: 10px;
    color: #7a3cff;
  }

  &__pro-icon {
    margin-right: 6px;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background-color: #7a3cff;
    display: flex;
    align-items: center;
    justify-content: center;

    svg {
      display: flex;
      width: 20px;
      height: 20px;

      * {
        fill: #fff;
      }
    }
  }

  &__combined-posts {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  &__combined-post-item {
    display: flex;
    align-items: center;
  }
}