.inavii-social-feed-app {
  &__hashtag-list {
    .ant-empty {
      margin: 0 0 10px;
    }

    .ant-list-empty-text {
      padding: 0;
    }

    .ant-empty-image svg {
      width: 50px;
    }

    .ant-list-item {
      &:first-child {
        padding-top: 0;
      }
    }
  }

  &__hashtag-item-box {
   position: relative;
    width: 100%;
  }

  &__hashtag-item-remove {
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
  }
}