.inavii-social-feed-app {
  $inavii-global-class: '.inavii-social-feed-app__';

  &__grid {
    svg {
      width: 16px;
    }
  }

  &__media-type {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 20px !important;
    z-index: 1;
    pointer-events: none;
  }

  &__feed-name {
    text-align: left;
  }

  &__feeds-container:not(#{$inavii-global-class}feeds-container-slider) {
    #{$inavii-global-class}grid {
      min-width: 800px;
      max-width: 1020px;
    }
  }

  &__grid {
    &:not(.inavii-social-feed-app__masonry-vertical):not(.inavii-social-feed-app__masonry-horizontal):not(.inavii-social-feed-app__slider):not(.inavii-social-feed-app__cards):not(.inavii-social-feed-app__shape-matrix):not(.inavii-social-feed-app__creative-grid) {
      display: grid;
      gap: 6px;
      grid-auto-flow: dense;
      line-height: initial;
    }

    &-item {
      position: relative;
      border-radius: 8px;
      overflow: hidden;
      cursor: pointer;
      align-self: flex-start;
    }

    &#{$inavii-global-class}gallery,
    &#{$inavii-global-class}row {
      gap: 0 !important;

      #{$inavii-global-class}grid-item {
        border-radius: 0;
      }
    }

    &#{$inavii-global-class}wave {
      padding-top: 24px;
    }

    &:not(.inavii-social-feed-app__cards):not(.inavii-social-feed-app__content-grid) {
      #{$inavii-global-class}box-description {
        align-items: flex-start;
        flex-direction: column;
        height: calc(100% - 20px);
        font-family: 'Cabin', sans-serif;
        justify-content: space-around;
        padding: 10px;
        width: calc(100% - 20px);
        left: 0;
        opacity: 0;
        position: absolute;
        top: 0;
        transform: scale(1.5);
        transition: all .3s;
        color: #FFFFFF;
        z-index: 2;
        pointer-events: none;

        p {
          margin: 0;
        }
      }

      #{$inavii-global-class}grid-item {
        &::before {
          content: " ";
          display: block;
          padding-top: 100%;
          width: 100%;
        }

        #{$inavii-global-class}grid-item-box {
          bottom: 0;
          left: 0;
          position: absolute;
          right: 0;
          top: 0;

          .ant-image-mask-info {
            display: none;
          }

          .ant-image,
          img {
            display: flex;
            height: 100%;
            object-fit: cover;
            transition: transform .2s;
          }
        }

        &:hover {
          img {
            transform: scale(1.1);
          }

          .inavii-social-feed-app__box-description {
            background-color: rgba(0, 0, 0, .8);
            opacity: 1;
            transform: scaleX(1);
          }
        }
      }
    }

    &:not(.inavii-social-feed-app__cards) {
      #{$inavii-global-class}item-box,
      .ant-image,
      .ant-image-img {
        height: 100%;
        width: 100%;
      }
    }
  }

  &__skeleton.ant-skeleton-image {
    position: absolute;
    top: 0;
    left: 0;
    height: 100% !important;
    width: 100% !important;

    .ant-skeleton-image-svg {
      display: none;
    }
  }

  &__demo {
    .inavii-social-feed-app__box-description {
      display: flex;
    }
  }

  &__top-box {
    display: block;
    overflow: auto;
    text-align: center;
    width: 100%;
  }

  &__avatar-box {
    align-items: center;
    display: flex;
    justify-content: flex-start;
    padding-bottom: 5px;
  }

  &__avatar-content {
    align-items: center;
    border: 1px solid #7a3cff;
    border-radius: 50%;
    display: flex;
    height: 32px;
    justify-content: center;
    margin-right: 10px;
    min-width: 32px;
    overflow: hidden;
    position: relative;
    width: 32px;
  }

  &__nickname,
  &__description {
    -webkit-box-orient: vertical;
    display: block;
    display: -webkit-box;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__nickname {
    word-wrap: break-word;
    -webkit-line-clamp: 1;
    font-size: 18px;
    font-weight: 600;
    text-align: left;
  }

  &__middle-box {
    width: 100%;
  }

  &__description {
    -webkit-line-clamp: 5;
    font-size: 13px;
    text-align: center;
  }

  &__icons-box {
    padding: 5px 0;
    width: 100%;

    svg {
      margin-right: 4px;
    }
  }

  &__icons-box,
  &__icons-box-right {
    align-items: center;
    display: flex;
    justify-content: center;
    width: 100%;
  }

  &__icon-text {
    font-size: 12px;
  }

  &__icons-box p {
    align-items: center;
    display: flex;
    justify-content: space-between;

    &:first-of-type {
      margin-right: 10px;
    }
  }

  &__grid:not(#{$inavii-global-class}slider):not(#{$inavii-global-class}cards) {
    grid-template-columns: repeat(5, 1fr);
  }

  &__highlight {
    grid-template-columns: repeat(6, 1fr) !important;
  }

  &__highlight-focus,
  &__highlight-super {
    grid-template-columns: repeat(4, 1fr) !important;
  }

  &__masonry-horizontal {
    column-count: 6;
    column-gap: 6px;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    row-gap: 6px;

    .inavii-social-feed-app__grid-item,
    .inavii-social-feed-app__feed-item {
      height: 150px;
    }
  }

  &__masonry-vertical {
    grid-template-columns: unset;
    column-gap: 6px;
    column-count: 5;

    .inavii-social-feed-app__grid-item {
      align-items: center;
      display: inline-flex;
      margin-bottom: 6px;
      width: 100%;

      &--height-0 {
        height: 160px;
      }

      &--height-1 {
        height: 175px;
      }

      &--height-2 {
        height: 190px;
      }

      &--height-3 {
        height: 205px;
      }

      &--height-4 {
        height: 220px;
      }

      &--height-5 {
        height: 235px;
      }

      &--height-6 {
        height: 250px;
      }

      &--height-7 {
        height: 260px;
      }

      &--height-8 {
        height: 270px;
      }

      &--height-9 {
        height: 280px;
      }

      &--height-10 {
        height: 290px;
      }
    }
  }

  &__slider {
    min-height: 260px;

    .inavii-social-feed-app__grid {
      &-item {
        height: 100%;

        &::before {
          display: none;
        }

        &-box {
          position: relative;
          display: inline-block;
          width: 100%;
          height: 100%;
        }
      }
    }

    .slick-list {
      .slick-slide {
        height: 220px;
        display: flex;

        > div:not(.inavii-social-feed-app__grid-item) {
          display: flex;
          column-gap: 6px;
          width: 100%;
        }
      }
    }

    .inavii-social-feed-app__slider-container {
      > .slick-arrow {
        &.slick-next {
          //inset-inline-end: 0 !important;
        }
      }
    }

    .slick-dots {
      position: relative;
      margin: 0;
      bottom: -20px;

      li.slick-active,
      li {
        button {
          background: #7a3cff;
        }
      }
    }

    .slick-arrow {
      opacity: 1 !important;

      &:not(.slick-disabled) {
        &:hover {
          opacity: .8 !important;
        }
      }

      svg > path {
        fill: #7a3cff !important;
      }

      &.slick-disabled {
        display: none !important;
        filter: grayscale(1);
        cursor: unset;
      }

      &.slick-next {
        inset-inline-end: -40px !important;
      }

      &.slick-prev {
        inset-inline-start: -40px !important;
      }
    }
  }

  #{$inavii-global-class}icons-box-left {
    display: inline-flex;
    white-space: nowrap;
  }

  #{$inavii-global-class}icons-box {
    gap: 10px;
  }

  #{$inavii-global-class}icons-box-right {
    display: inline-flex;
    justify-content: flex-end;
    gap: 5px;
  }

  #{$inavii-global-class}box-description {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  &__cards {
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    align-items: flex-start;
    display: grid;
    min-height: 400px;

    #{$inavii-global-class}box-description {
      display: flex;
      flex-direction: column;
      gap: 5px;
      color: #000;
      padding: 10px;

      p {
        margin: 0;
        text-align: left;
      }
    }

    #{$inavii-global-class}grid-item-box {
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    #{$inavii-global-class}grid-item {
      box-shadow: 0 0 0 rgba(0, 0, 0, .0);
      width: 100%;
      border: 1px solid #d9d9d9;
      align-self: flex-start;
      box-sizing: border-box;
      display: flex;
      height: 200px;
      min-width: 100%;
      grid-row-end: span 21;
      transition: box-shadow .3s;

      .inavii-social-feed-app__skeleton {
        position: relative;

        &::before {
          content: '';
          display: block;
          padding-top: 100%;
        }
      }

      &:hover {
        box-shadow: 0 0 15px rgba(0, 0, 0, .20);
      }
    }
  }

  &__shape-matrix {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
    font-family: sans-serif;
    list-style-type: none;

    #{$inavii-global-class}grid-item {
      position: relative;
      visibility: hidden;
      overflow: visible;
      outline: 1px solid transparent;
      transition: all 0.5s;
      backface-visibility: hidden;
      will-change: transform;

      &::after {
        content: '';
        display: block;
        padding-bottom: 86.602%;
        pointer-events: none;
      }

      &::before {
        display: none!important;
      }

      > a {
        position: absolute;
        left: 0!important;
        bottom: unset!important;
        right: unset!important;
        top: unset!important;
        width: 96%;
        padding-bottom: 110.851%;
        margin: 2%;
        overflow: hidden;
        visibility: visible;
        outline: 1px solid transparent;
        transition: all 0.5s;

        * {
          position: absolute;
          visibility: visible;
          outline: 1px solid transparent;
        }

        .inavii-social-feed-app__item-box {
          display: block;
          width: 100%;
          height: 100%;
          text-align: center;
          color: #fff;
          overflow: hidden;
          -webkit-clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
          clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);

          .inavii-social-feed-app__description {
            -webkit-line-clamp: 4;
          }

          img,
          .ant-image {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            height: 100%!important;
            background-position: center center;
            background-size: cover;
            overflow: hidden;
          }
        }
      }

      #{$inavii-global-class}box-description {
        height: auto!important;
        padding: 28% 10px!important;
        bottom: 0;

        * {
          position: relative;
        }
      }
    }

    @media (min-width: 1201px) {
      padding-bottom: 5.3%;

      #{$inavii-global-class}grid-item {
        width: 20%;
      }
      #{$inavii-global-class}grid-item:nth-child(9n+6) {
        margin-left: 10%;
      }
    }

    @media (max-width: 1200px) and (min-width: 901px) {
      padding-bottom: 6.6%;

      #{$inavii-global-class}grid-item {
        width: 25%;
      }
      #{$inavii-global-class}grid-item:nth-child(7n+5) {
        margin-left: 12.5%;
      }
    }

    @media (max-width: 900px) and (min-width: 601px) {
      padding-bottom: 8.8%;

      #{$inavii-global-class}grid-item {
        width: 33.333%;
      }
      #{$inavii-global-class}grid-item:nth-child(5n+4) {
        margin-left: 16.666%;
      }
    }

    @media (max-width: 600px) {
      padding-bottom: 13.3%;

      #{$inavii-global-class}grid-item {
        width: 50%;
      }
      #{$inavii-global-class}grid-item:nth-child(3n+3) {
        margin-left: 25%;
      }
    }
  }

  &__content-grid {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: 0!important;

    #{$inavii-global-class}box-description {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 20px;
      color: #000;
      padding: 20px 0;
      background-color: #FFFCF5;
      transition: background-color .3s;

      p {
        margin: 0;
        text-align: center;
      }

      .inavii-social-feed-app__middle-box,
      .inavii-social-feed-app__icons-box {
        padding: 0 15px;
        width: calc(100% - 30px);
      }
    }

    #{$inavii-global-class}grid-item-box {
      display: flex;
      flex-direction: column;
      width: 100%;
      border-radius: 0;
    }

    #{$inavii-global-class}grid-item {
      border-radius: 0;
      display: flex;
      align-self: stretch;

      &:nth-child(4n+3) #{$inavii-global-class}item-box,
      &:nth-child(4n+4) #{$inavii-global-class}item-box {
        flex-direction: row-reverse;
      }

      img,
      video {
        aspect-ratio: 1/1;
        object-fit: cover;
      }

      &:hover {
        #{$inavii-global-class}box-description {
        background-color: #fbecc7;}
      }
    }

    #{$inavii-global-class}item-box {
      display: flex;

      > * {
        width: 50%;
      }
    }
  }

  &__creative-grid {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
    font-family: sans-serif;
    list-style-type: none;

    #{$inavii-global-class}grid-item {
      position: relative;
      visibility: hidden;
      overflow: visible;
      outline: 1px solid transparent;
      transition: all 0.5s;
      backface-visibility: hidden;
      will-change: transform;

      &::after {
        content: '';
        display: block;
        padding-bottom: 50%;
        pointer-events: none;
      }

      &::before {
        display: none!important;
      }

      > a {
        position: absolute;
        left: 0!important;
        bottom: unset!important;
        right: unset!important;
        top: unset!important;
        width: 96%;
        padding-bottom: 96%;
        margin: 2%;
        overflow: hidden;
        visibility: visible;
        outline: 1px solid transparent;
        transition: all 0.5s;

        * {
          position: absolute;
          visibility: visible;
          outline: 1px solid transparent;
        }

        .inavii-social-feed-app__item-box {
          display: block;
          width: 100%;
          height: 100%;
          text-align: center;
          color: #fff;
          overflow: hidden;
          -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
          clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
          transition: all 0.3s;

          .inavii-social-feed-app__description {
            -webkit-line-clamp: 4;
          }

          img,
          .ant-image {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            height: 100%!important;
            background-position: center center;
            background-size: cover;
            overflow: hidden;
          }
        }
      }

      #{$inavii-global-class}box-description {
        height: auto!important;
        padding: 20px!important;
        justify-content: space-between!important;
        bottom: 0;
        width: calc(100% - 40px)!important;

        * {
          position: relative;
        }
      }

      &:hover {
        z-index: 2;

        .inavii-social-feed-app__item-box {
          clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
        }

      }
    }

    @media (min-width: 1201px) {
      padding-bottom: 16%;

      #{$inavii-global-class}grid-item {
        width: 33.33%;
      }

      #{$inavii-global-class}grid-item:nth-child(5n+4) {
        margin-left: 16.665%;
      }
    }

    @media (max-width: 1200px) and (min-width: 901px) {
      padding-bottom: 16%;

      #{$inavii-global-class}grid-item {
        width: 33.33%;
      }

      #{$inavii-global-class}grid-item:nth-child(5n+4) {
        margin-left: 16.665%;
      }
    }

    @media (max-width: 900px) and (min-width: 601px) {
      padding-bottom: 27%;

      #{$inavii-global-class}grid-item {
        width: 33.333%;
      }
      #{$inavii-global-class}grid-item:nth-child(5n+4) {
        margin-left: 16.665%;
      }
    }

    @media (max-width: 600px) {
      padding-bottom: 65%;

      #{$inavii-global-class}grid-item {
        width: 50%;
      }
      #{$inavii-global-class}grid-item:nth-child(3n+3) {
        margin-left: 25%;
      }
    }
  }

  &__edit-feed {
    &#{$inavii-global-class}slider {
      display: grid;
      width: 100%;
      grid-auto-flow: column;
      grid-auto-rows: max-content;
      box-sizing: border-box;
      min-width: 350px;
      grid-gap: 8px;
      overflow: auto;

      #{$inavii-global-class}feed-item {
        height: 220px;
        width: 220px;
      }
    }
  }
}