.inavii-social-feed-app {
  $self: &;

  &__feed-image {
    height: 100% !important;
    width: 100%;
    object-fit: cover;
  }

  #{$self}__masonry-vertical {
    #{$self}__feed-item {
      align-items: center;
      display: inline-flex;
      margin-bottom: 6px;
      width: 100%;
    }
  }

  &__grid {
    &:not(.inavii-social-feed-app__cards):not(.inavii-social-feed-app__shape-matrix):not(.inavii-social-feed-app__content-grid):not(.inavii-social-feed-app__creative-grid) {
      #{$self}__feed-item {
        aspect-ratio: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        transform-origin: 0 0;
      }

      #{$self}__feed-item-box {
        width: 100%;
        height: 100%;
        display: flex;
        position: relative;

        .ant-image {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
        }

        .video-placeholder,
        .ant-image-img {
          width: 100%;
          aspect-ratio: 1/1;
          object-fit: cover;
        }
      }
    }
  }

  &__grid {
    &:not(#{$self}__gallery):not(#{$self}__row) {
      #{$self}__feed-item {
        border-radius: 8px;
      }
    }

    &#{$self}__wave {
      #{$self}__feed-item {
        height: fit-content;
      }
    }
  }

  &__feed-item {
    &:focus,
    &:hover,
    #{$self}__feed-item-active {
      #{$self}__feed-center-box {
        opacity: 1;
      }
    }

    &.inavii-pointer {
      cursor: pointer;
    }

    &.locked {
      cursor: not-allowed;
    }
  }

  &__feed-open-modal,
  &__feed-is-locked {
    position: absolute;
    left: 5px;
    cursor: pointer;
    z-index: 2;
    translate: 0;
  }

  &__feed-open-modal {
    top: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    border-radius: 50%;
    height: 30px;
    width: 30px;

    &:hover {
      background: #9c66ff !important;
    }

    svg {
      width: 16px;
      height: 16px;
      object-fit: contain;
    }

    * {
      pointer-events: none;
    }
  }

  &__feed-is-locked {
    bottom: 5px;
  }

  &__feed-is-locked,
  &__feed-show-hidden {
    img {
      width: 20px;
      height: 20px;
      object-fit: contain;
    }

    &.ant-switch.ant-switch-checked .ant-switch-inner {
      padding-inline-end: 14px;
    }

    .ant-switch-inner {
      display: flex;
      align-items: center;
      padding-inline-start: 14px;
    }

    .ant-switch-inner-checked {
      display: inline-block !important;
      margin-top: 0 !important;
    }

    .ant-switch-inner-unchecked {
      display: inline-block !important;
      margin-top: 0 !important;
    }
  }

  &__feed-center-box {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 56px;
    width: 56px;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 1px solid #7a3cff;
    background-color: #fff;
    z-index: 2;
    cursor: grab;
    opacity: 0;
    visibility: visible;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    svg {
      width: 40px;
      height: 40px;
      object-fit: contain;
    }

    &.locked {
      cursor: not-allowed;

      svg {
        margin-top: 1px;
        margin-left: 1px;
        width: 40px;
        height: 40px;
      }
    }
  }

  &__feed-show-hidden {
    position: absolute;
    right: 5px;
    bottom: 5px;
    z-index: 2;
    translate: 0;
  }

  &__feed-item-hidden {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backdrop-filter: blur(5px);
      background-color: rgba(#fff, 0.5);
      z-index: 1;
    }
  }

  &__feeds-container:not(#{$self}__feeds-container-slider) {
    overflow: auto;
  }

  &__cards {
    width: 100%;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    align-items: flex-start;
    display: grid;
    min-height: 400px;

    #{$self}__feed-item-box {
      position: relative;
      border: 1px solid #d9d9d9;
      border-radius: 8px;
      overflow: hidden;
    }

    #{$self}__feed-item {
      box-shadow: 0 0 0 rgba(0, 0, 0, .0);
      width: 100%;
      align-self: flex-start;
      box-sizing: border-box;
      display: flex;
      height: 200px;
      min-width: 100%;
      grid-row-end: span 21;
      transition: box-shadow .3s;

      .inavii-social-feed-app__skeleton {
        position: relative;

        &::before {
          content: '';
          display: block;
          padding-top: 100%;
        }
      }

      &:hover {
        box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
      }
    }
  }

  &__shape-matrix,
  &__creative-grid {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
    font-family: sans-serif;
    list-style-type: none;

    .inavii-social-feed-app__feed-item {
      position: relative;
      visibility: hidden;
      overflow: visible;
      outline: 1px solid transparent;
      backface-visibility: hidden;
      will-change: transform;
      display: flex;
      justify-content: center;
      align-items: flex-start;

      &::after {
        content: '';
        display: block;
        padding-bottom: 86.602%;
        pointer-events: none;
      }

      &.inavii-social-feed-app__feed-item-hidden {
        img {
          filter: blur(5px);
        }
      }

      .inavii-social-feed-app__description {
        -webkit-line-clamp: 4;
      }

      .inavii-social-feed-app__feed-item-box {
        position: absolute;
        left: 0!important;
        bottom: unset!important;
        right: unset!important;
        top: unset!important;
        background-color: transparent!important;
        width: 96%;
        padding-bottom: 110.851%;
        margin: 2%;
        overflow: hidden;
        visibility: visible;
        outline: 1px solid transparent;
        transition: all 0.5s;

        .inavii-social-feed-app__item-box,
        .inavii-social-feed-app__item-box *:not(.inavii-social-feed-app__feed-center-box *):not(.inavii-social-feed-app__feed-is-locked *):not(.inavii-social-feed-app__feed-show-hidden *):not(.inavii-social-feed-app__feed-open-modal *) {
          position: absolute;
          visibility: visible;
          outline: 1px solid transparent;
        }

        .inavii-social-feed-app__feed-open-modal {
          top: 15px;
          left: 50%;
          transform: translateX(-50%);
        }

        .inavii-social-feed-app__feed-is-locked {
          bottom: 45px;
          left: 25px;
        }

        .inavii-social-feed-app__feed-show-hidden {
          bottom: 45px;
          right: 25px;
        }

        .inavii-social-feed-app__item-box {
          display: block;
          width: 100%;
          height: 100%;
          text-align: center;
          color: #fff;
          overflow: hidden;
          -webkit-clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
          clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);

          img,
          .ant-image {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            height: 100%!important;
            background-position: center center;
            background-size: cover;
            overflow: hidden;
          }
        }
      }

      .inavii-social-feed-app__box-description {
        height: auto;
        padding: 28% 10px;

        * {
          position: relative;
        }
      }
    }

    &:not(.inavii-social-feed-app__creative-grid) {
      @media (min-width: 1201px) {
        padding-bottom: 5.3%;

        .inavii-social-feed-app__feed-item {
          width: 20%;
        }
        .inavii-social-feed-app__feed-item:nth-child(9n+6) {
          margin-left: 10%;
        }
      }

      @media (max-width: 1200px) and (min-width: 901px) {
        padding-bottom: 6.6%;

        .inavii-social-feed-app__feed-item {
          width: 25%;
        }
        .inavii-social-feed-app__feed-item:nth-child(7n+5) {
          margin-left: 12.5%;
        }
      }

      @media (max-width: 900px) and (min-width: 601px) {
        padding-bottom: 8.8%;

        .inavii-social-feed-app__feed-item {
          width: 33.333%;
        }
        .inavii-social-feed-app__feed-item:nth-child(5n+4) {
          margin-left: 16.666%;
        }
      }

      @media (max-width: 600px) {
        padding-bottom: 13.3%;

        .inavii-social-feed-app__feed-item {
          width: 50%;
        }
        .inavii-social-feed-app__feed-item:nth-child(3n+3) {
          margin-left: 25%;
        }
      }
    }
  }

  &__content-grid {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: 0!important;

    .inavii-social-feed-app__box-description {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 20px;
      color: #000;
      padding: 20px 0;
      background-color: #FFFCF5;
      transition: background-color .3s;

      p {
        margin: 0;
        text-align: center;
      }

      .inavii-social-feed-app__middle-box,
      .inavii-social-feed-app__icons-box {
        padding: 0 15px;
        width: calc(100% - 30px);
      }
    }

    .inavii-social-feed-app__feed-item-box {
      display: flex;
      flex-direction: column;
      width: 100%;
      border-radius: 0;
    }

    .inavii-social-feed-app__feed-item {
      border-radius: 0;

      &:nth-child(4n+3) .inavii-social-feed-app__item-box,
      &:nth-child(4n+4) .inavii-social-feed-app__item-box {
        flex-direction: row-reverse;
      }

      img,
      video {
        aspect-ratio: 1/1;
      }

      &:hover {
        .inavii-social-feed-app__box-description {
          background-color: #fbecc7;}
      }
    }

    .inavii-social-feed-app__item-box {
      display: flex;
      position: relative;

      > * {
        flex: 1;
      }
    }
  }

  &__creative-grid {
    .inavii-social-feed-app__feed-item {
      &::after {
        padding-bottom: 50%;
      }

      .inavii-social-feed-app__feed-item-box {
        width: 96%;
        padding-bottom: 96%;

        .inavii-social-feed-app__feed-is-locked {
          bottom: 50%;
          left: 15px;
          transform: translateY(50%);
        }

        .inavii-social-feed-app__feed-show-hidden {
          bottom: 50%;
          right: 15px;
          transform: translateY(50%);
        }

        .inavii-social-feed-app__item-box {
          -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
          clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
          transition: all 0.3s;
        }
      }

      .inavii-social-feed-app__box-description {
        padding: 20px!important;
        justify-content: space-between!important;
        width: calc(100% - 40px)!important;
      }
    }
  }
}